.tabs {
  position: relative;
  padding-top: 60px;
  height: 100%;
  color: $darkGray;
  background: $alabaster;
  &_lg {
    .tabs__btn {
      font-size: 16px; } }
  &__controls {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    display: table;
    table-layout: fixed;
    width: 100%; }
  &__list {
    display: table-row; }
  &__item {
    display: table-cell;
    vertical-align: top;
    &:not(:last-child) {
      border-right: 2px solid rgba($black, 0.1); } }
  &__btn {
    display: block;
    padding: 20px 10px;
    color: inherit;
    background: $lightGray;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    text-decoration: none;
    @include nosel;
    &.is-active {
      background: $alabaster; }
    &.is-disabled {
      color: rgba($black, 0.3);
      pointer-events: none; } }
  &__container {
    height: 100%;
    background: $alabaster; }
  &__content {
    display: none;
    overflow: auto;
    padding: 20px;
    height: 100%;
    &.is-open {
      display: block; } }
  &__keyboard,
  &__notes {
    padding-top: 10px;
    margin: 0 auto;
    max-width: 460px; }
  &__info {
    height: 100%; }
  &__notes {
    .fieldset {
      margin-bottom: 20px; } } }

